<template>
    <v-layout>
        <v-flex xs12 sm4 offset-sm4>
          <v-card>
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
              class="contact-form pa-3"
            >
              <v-text-field
                v-model="name"
                :counter="10"
                :rules="nameRules"
                label="Name"
                required
              ></v-text-field>

              <v-text-field
                v-model="email"
                :rules="emailRules"
                label="E-mail"
                required
              ></v-text-field>

              <v-textarea
                v-model="text"
                :rules="[v => !!v || 'Text is required.']"
                label="Text"
                required
              ></v-textarea>

              <v-checkbox
                v-model="checkbox"
                :rules="[v => !!v || 'You must agree to continue.']"
                label="I am not a bot."
                required
              ></v-checkbox>

              <v-btn
                :disabled="!valid"
                color="success"
                @click="validate"
              >
                Send
              </v-btn>
            </v-form>
          </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
export default {
  name: 'Contacts',
  data() {
    return {
      valid: true,
      name: '',
      nameRules: [
          v => !!v || 'Name is required',
          v => (v && v.length <= 10) || 'Name must be less than 10 characters.'
      ],
      email: '',
      emailRules: [
          v => !!v || 'E-mail is required',
          v => /.+@.+/.test(v) || 'E-mail must be valid.'
      ],
      text: '',
      checkbox: false
    }
  },
  methods: {
    validate () {
      if (this.$refs.form.validate()) {
        Email.send({
          SecureToken: "fa0bfe19-5db8-492a-9c9f-1f95bc45559c",
          To: 'thejudge999@abv.bg',
          From: this.email,
          Subject: this.name,
          Body: this.text
        }).then(
          message => alert(message)
        );
      }
    },
    reset () {
      this.$refs.form.reset()
    },
    resetValidation () {
      this.$refs.form.resetValidation()
    }
  }
}
</script>

