<template>
    <v-layout>
        <v-flex sm6 offset-sm3>
            <v-card class="pa-4">
                <h1 class="pb-2">{{ article.title }}</h1>
                <v-divider></v-divider>
                <p class="pt-3">{{ article.text }}</p>
            </v-card>
        </v-flex>
    </v-layout> 
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'Article',
    data() {
        return {
            article: {}
        }
    },
    methods: {
        ...mapActions(['fetchArticles'])
    },
    computed: {
        ...mapGetters(['getArticle'])
    },
    created() {
        this.fetchArticles().then(articles => {
            this.article = this.getArticle(this.$route.params.id)
        });
    }
}
</script>

